.error-block {
  display: flex;
  flex-direction: column;
  gap: 113px;
  padding-bottom: 100px;
  padding-top: 100px;
}
@media (min-width: 1024px) {
  .error-block {
    gap: 139px;
  }
}

.oups {
  display: flex;
  text-align: center;
  justify-content: center;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .oups {
    gap: 5px;
    flex-direction: row;
  }
}

.error-span {
  color: #053269;
  font-family: Vujahday Script;
  font-size: 96px;
  font-weight: 700;
  text-align: center;
}
@media (min-width: 1024px) {
  .error-spanspan {
    font-size: 288px;
    line-height: 250px;
  }
}

.oups h2 {
  font-family: Vujahday Script;
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
  color: #053269;
}
@media (min-width: 1024px) {
  .oups h2 {
    font-size: 36px;
    line-height: 51px;
  }
} 

.error-link {
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  color: #000000;
  display: flex;
  justify-content: center;
}
@media (min-width: 1024px) {
  .error-link {
    font-family: Montserrat, sans-serif;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
}

